/* 圆形 */

.br {
    border-radius: 50%;
}

.por {
    position: relative;
}

/* 浮动 */

.fl {
    float: left
}

.fr {
    float: right
}

.bgfff {
    background: #fff;
}

.bsbb {
    box-sizing: border-box;
}

.text-right {
    text-align: right;
}

.flex-wrap {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
}

/* 字超出省略 */

.ell1 {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.ell2 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.icon-down {
    transform: rotate(-90deg);
}

.icon-right {
    transform: rotate(180deg);
}

a:link {
    color: #000000;
    text-decoration: none;
}

a:visited {
    color: #000000;
    text-decoration: none;
}

a:hover {
    color: #999999;
    text-decoration: underline;
}

// 重置富文本中的图片样式
.vhtml img {
    width: 100%;
}

//   查看更多的悬浮按钮
.look-more-view .img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 82px;
    height: 82px;
}

.look-more-view {
    position: fixed;
    right: 32px;
    bottom: 280px;
    overflow: hidden;
    width: 76px;
    height: 76px;
}

// 返回
.back {
    position: fixed;
    right: 32px;
    bottom: 180px;
    overflow: hidden;
    width: 72px;
    height: 72px;
    background: #5581ff;
    border-radius: 50%;
    color: #fff;
}

.back .back-icon {
    font-size: 32px;
    font-weight: 600;
    color: #fff;
}

.touch-none {
    // touch-action: none;
  }


